@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Tilt+Neon&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;

}

a {
  text-decoration: none;

}


html {
  font-size: 62.5%;
}



/* search part css  */
.search-section {
  display: flex;
  justify-content: space-between;
  height:11.2vh;
  padding: 2.5rem 5.5rem;
  background: linear-gradient(rgb(39, 39, 39), rgb(20, 20, 20));
  


}

.search-section .left {
  display: flex;
  width: 50%;
  align-items: center;

}

.search-section .left .brand {
  font-size: 2rem;
  font-family: 'Comfortaa', cursive;
  color: red;
  
}

.search-section .left ul {
  display: flex;
  justify-content: space-evenly;
  width: 40%;
  margin: 0 0 0 1rem;
  color: white;
  list-style-type: none;
  

}

.search-section .left ul li .nav-link {
  color: white;
  font-size: 1.2rem;
  transition: .2s ease-in;
  
  



}

.search-section .left ul li .nav-link:hover {
  color: white;
  font-size: 1.4rem;
  


  



}



.search-section .right {
  color: white;
  

}

.search-section .right .input-part {
  background-color: #f2f4fc;
  padding: 0 1.3rem 0 0;
  border-radius: 2rem;
  background-color: rgb(182, 44, 19);
  color: white;
  margin: 0 0 0 3rem;

}

.search-section .right .input-part input {
  border: none;
  outline: none;
  padding: 1.2rem 3rem;
  background-color: rgb(65, 65, 65);
  color: white;
  


}

.search-section .right .input-part button {
  border: none;
  background-color: rgb(188, 3, 3);
  color: white;
  padding: 0 0 0 .5rem;

}

.search-section .right .input-part button a {
  color: white;
  width: 50rem;
  height: 50rem;
  transform: scale(5);
}


.results {
  padding: 2.5rem 6rem 4rem 1rem;
  font-size: 3rem;
  color: white;
  background: rgb(21, 21, 21);

}

/*Home part css*/

.movie-section {
  padding: 2.5rem 7rem;
  background-color: rgb(21, 21, 21);

}


.movie-section .card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;




}

.movie-section .card .card-info {
  margin: 0 0 5rem 0;
}

.movie-section .card .card-info .image {
  position: relative;





}

.movie-section .card .card-info .image .overlay {
  top: 0;
  opacity: 0;
  position: absolute;
  background: linear-gradient(rgba(255, 0, 0, 0), rgba(255, 0, 0, 0.213));

  height: 53rem;
  width: 37rem;
  border-radius: .5rem;
  transition: .5s ease;
  color: rgb(255, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;

}

.movie-section .card .card-info .image .overlay:hover {
  opacity: 1;
}

.movie-section .card .card-info .image .overlay .type {
  opacity: 1;
  color: rgb(255, 255, 255);
  font-weight: 1000;


}

.movie-section .card .card-info .image .overlay .type:hover {
  opacity: 1;
  color: white;


}

.movie-section .card .card-info .image img {
  height: 53rem;
  width: 37rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  border-radius: .5rem;

}

.movie-section .card .card-info .movie-details {
  padding: 1rem 1rem 0 .7rem;
  display: flex;
  justify-content: space-between;
  width: 37rem;

}

.movie-section .card .card-info .movie-details h2 {
  font-size: 1.8rem;
  width: 80%;
  color: white;
  font-family: 'Work Sans', sans-serif;
}

.movie-section .card .card-info .movie-details span {
  font-size: 1.8rem;
  color: gold;
  font-weight: 800;
  letter-spacing: .1rem;
  font-family: 'Roboto Slab', serif;


}

/* Single Movie css code  */
.movie-details-section {
  background-color: rgb(21, 21, 21);
  height: 88.8vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.movie-details-section .details-container {
  background-color: rgb(255, 255, 255);
  color: white;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 1rem;
  width: 60vw;
  height: 60vh;
  border-radius: 1rem;


}

.movie-details-section .details-container .left {
  width: 100%;
  height: 100%;
  position: relative;
}


.movie-details-section .details-container .left img {
  width: 100%;
  height: 100%;
  position: absolute;


}

.movie-details-section .details-container .left .details-overlay {
  background-image: linear-gradient(to right, #f7dada00, #ff00000c, #ff00000b, #00000011, #b0b0b018, #ff00002c, #ffc0c048, #ffe0e063, #fbf3f37f, #ffffff);


  color: rgb(127, 0, 0);
  width: 100%;
  height: 100%;
  position: absolute;
}

.movie-details-section .details-container .right {
  padding: 3rem 3rem 2rem 2rem;
  color: black;
}

.movie-details-section .details-container .right .top-part {}

.movie-details-section .details-container .right .top-part .movie-name {
  font-weight: 800;
  font-size: 4rem;
  font-family: 'Roboto Slab', serif;
}

.movie-details-section .details-container .right .top-part .second-part {
  display: flex;
  font-size: 1.5rem;
  margin: .7rem 0 0 0;
  letter-spacing: .1rem;



}



.movie-details-section .details-container .right .top-part .third-part {
  margin: 2rem 0 0 0;
  display: flex;
  align-items: center;
  font-size: 1.5rem;
}

.movie-details-section .details-container .right .top-part .third-part .runtime {
  margin: 0 1.2rem 0 0;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  border-radius: .6rem;
  padding: .5rem .4rem;
}

.movie-details-section .details-container .right .top-part .third-part .genre{
  font-family: 'Roboto Slab', serif;
}

.movie-details-section .details-container .right .mid-part {

  margin: 3rem 0 0 0;
}

.movie-details-section .details-container .right .mid-part .plot {
  font-size: 1.8rem;
  font-family: 'Noto Sans', sans-serif;
  font-weight:300;

}

.movie-details-section .details-container .right .mid-part .awards {

  padding: 3rem 2rem 0 0;
  margin: 2rem 0 0 0;
  font-size: 1.5rem;
  display: flex;
  justify-content: space-between;
  
  

}

.movie-details-section .details-container .right .mid-part .awards  span{
  font-family: 'Josefin Sans', sans-serif;

}

.movie-details-section .details-container .right .mid-part .awards .imdb{
  padding:.6rem .4rem;
  background-color: rgb(243, 212, 38);
  border-radius: .5rem;

}



/* Latest Page */




/*   Loading page */

.loading {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(rgb(20, 20, 20), rgb(0, 0, 0));


}

.loading .loader {
  transition: 1s ease;
  transform: scale(1.5);
}

.movie-details-loading {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 0, 0);

}

/* Slider section */

.slider-section {

  background-color: rgb(21, 21, 21);
  padding: 2rem 0;




}

.slider-section .box .prev {
  position: absolute;
  top: 50%;
  left: 15%;
  background: rgba(128, 128, 128, 0.621);
  border: none;

  color: white;
  font-size: 8rem;
  border-radius: 1rem;
  padding: 0 1rem;
  transition: .3s ease-out;
  cursor: pointer;


}

.slider-section .box .next {
  position: absolute;
  top: 50%;
  left: 82%;
  background: rgba(128, 128, 128, 0.621);
  border: none;
  color: white;
  font-size: 8rem;
  border-radius: 1rem;
  padding: 0 1rem;
  transition: .3s ease-out;
  cursor: pointer;


}

.slider-section .box button:hover {
  color: rgb(214, 11, 11);

}

.slider-section .box {
  display: grid;
  place-items: center;
}



.slider-container {
  position: relative;
  overflow: hidden;
  width: 75vw;
  height: 85vh;
}

.slider-section .slider-container img {
  position: absolute;
  width:100%;
  height: 100%;
}





/* responsive code   */